<template>
  <div class="box">
    <Header :aot="true" :gnt="{name:mage.name}" :cat="true" slot="search">
      <i class="el-icon-search"></i>
    </Header>
    <div class="loding" v-if="name">
      <img src=".././page/images/4.gif" alt />
    </div>
    <div class="content">
      <div class="lun-bo">
        <van-swipe :autoplay="3000" indicator-color="white">
          <van-swipe-item>
            <ul>
              <li v-for="(item,index) of one[0]" :key="index">
                <p>
                  <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt />
                </p>
                <p>{{item.title}}</p>
              </li>
            </ul>
          </van-swipe-item>
          <van-swipe-item>
            <ul>
              <li v-for="(item,index) of one[1]" :key="index">
                <p>
                  <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt />
                </p>
                <p>{{item.title}}</p>
              </li>
            </ul>
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="item">
        <div class="nearby">
          <i class="el-icon-bell"></i>附近商家
        </div>

        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <div class="nr-item" v-for="(item,index) of list" :key="index" @click="xiang(item)">
             <!-- <router-link :to='/details/+item.id'> -->
            <div>
              <img :src="'//elm.cangdu.org/img/'+item.image_path" alt />
            </div>
            <div>
              <div class="pai">
                <div>{{item.name}}</div>
              </div>
              <div>
                <ul>
                  <li>
                    <el-rate
                      :value="item.rating"
                      disabled
                      show-score
                      text-color="#ff9900"
                      score-template="{value}"
                    ></el-rate>
                  </li>
                  <li>月售{{item.recent_order_num}}单</li>
                  <li>
                    <button class="but">蜂鸟专送</button>
                    <button class="but">准时达</button>
                  </li>
                </ul>
              </div>
              <div>
                <span>￥20起送/配送费约￥5</span>
                <span>{{item.distance}}/{{item.order_lead_time}}</span>
              </div>
            </div>
            <!-- </router-link> -->
          </div>
          
        </van-list>
      </div>
    </div>
    <Footer :not="this.$route.query.geohash"></Footer>
  </div>
</template>

<script>
import Header from "../page/header/Header";
import Footer from "../page/footer/Footer";
export default {
  data() {
    return {
      one: [],
      list: [],
      mage: "",
      name: true,
      loading: false,
      finished: false,
      limit: 0
    };
  },
  components: {
    Header,
    Footer
  },
  methods: {
    xiang(item){
        this.$router.push({path:'/details?geohash=45.725418,126.62506', query:{id:item.id}})
        // this.$router.push({path:'/details', query:{name:item}})
    },
    onLoad() {
      setTimeout(() => {
        if ((this.limit += 10)) {
          this.http
            .get(
              "http://elm.cangdu.org/shopping/restaurants?latitude=31.22299&longitude=121.36025&offset=0&limit=" +
                this.limit +
                "&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]="
            )
            .then(res => {
              // console.log(res.data)
              this.list = res.data;
            });
          for (let i = 0; i < 10; i++) {
            this.list.push(this.list.length + 1);
          }
          // 加载状态结束
          this.loading = false;

          if (this.list.length >= 100) {
            this.finished = true;
          }
        }
        // console.log(this.list)
        // 数据全部加载完成
      }, 1000);
    }
  },
  mounted() {
      this.onLoad()
    //  this.shopLoding = true;
    this.http
      .get(
        "http://elm.cangdu.org/v2/index_entry?geohash=39.78493,116.49476&group_type=1&flags[]=F"
      )
      .then(res => {
        setTimeout(() => {
          this.name = false;
        }, 1000);
        if (res.data.length > 0) {
          let resArr = res.data.slice(0);
          let oneArr = [];
          for (let i = 0, j = 0; i < res.data.length; i += 8, j++) {
            oneArr[j] = resArr.splice(0, 8);
          }
          this.one = oneArr;
        }
      });
        let geohash = this.$route.query.geohash;
        this.http.get(`http://elm.cangdu.org/v2/pois/${geohash}`).then(res => {
          this.mage = res.data;
        });
  }
};
</script>

<style lang="scss" scoped>
.box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-size: 0.35rem;
}
.loding {
  width: 100%;
  height: 100%;
  img {
    width: 8rem;
    padding-top: 2rem;
  }
}
.content {
  width: 100%;
  flex: 1;
  overflow: auto;
  font-size: 0.35rem;
  // background:red;
}
.lun-bo {
  width: 100%;
  height: 3.7rem;
  border-bottom: 0.2rem solid rgb(241, 227, 227);
  ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    li {
      width: 25%;
      height: 1.5rem;
      text-align: center;
      display: block;
      margin: 0.1rem 0;
      font-size: 0.28rem;
      img {
        width: 1rem;
      }
    }
  }
}
.nearby {
  width: 100%;
  height: 1rem;
  line-height: 1rem;
  padding: 0 0.2rem;
  color: rgb(197, 162, 162);
  font-size: 0.3rem;
  // background: wheat;
}
.nr-item {
  width: 100%;
  display: flex;
  height: 2.5rem;
  // background: wheat;
  border-bottom: 1px solid rgb(207, 191, 191);
  div:nth-of-type(1) {
    width: 25%;
    img {
      width: 1.8rem;
      height: 1.8rem;
      margin: 0 0.1rem;
      margin-top: 0.25rem;
      padding: 0.1rem;
    }
  }
  div:nth-of-type(2) {
    width: 75%;

    div {
      height: 0.8rem;
      line-height: 0.8rem;
    }
    div:nth-of-type(1) {
      width: 100%;
      // display: flex;
      font-size: 0.3rem;
      margin-left: 0.1rem;
      color: yellowgreen;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 1.6rem;
    }
    div:nth-of-type(2) {
      width: 100%;
      font-size: 0.23rem;
      ul {
        display: flex;
        li:nth-of-type(1) {
          flex: 1;
          color: rgb(153, 153, 49);
        }
        li:nth-of-type(2) {
          flex: 1;
          padding-left: 0.5rem;
        }
        li:nth-of-type(3) {
          flex: 2;
          font-size: 0.2rem;
          color: white;
          margin-left: 0.5rem;
          bitton {
            outline: none;
            border: none;
            color: white;
            height: 0.5rem;
          }
          button:nth-of-type(1) {
            background: rgb(141, 102, 177);
          }
        }
      }
    }
    div:nth-of-type(3) {
      width: 100%;
      font-size: 0.25rem;
      span:nth-of-type(2) {
        margin-left: 0.3rem;
      }
    }
  }
}
.but {
  width: 1rem;
  height: 0.4rem;
  line-height: 0.4rem;
  color: rgb(224, 199, 199);
}
</style>


